// src/i18n.js
import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";

// Translation resources
const resources = {
  he: {
    translation: {
      "Spyfall": "",
      "How Would You Like to Play?": "  ?",
      "Single Device":" ",
      "Create Room":" ",
      "Game Settings": " ",
      "Total players": " ",
      "Total spies": " ",
      "Start Game": " ",
      "Would you find the Spy?":"   ?",
      "Player":"",
      "Start New Game":"  ",
      "You're The Spy": "/ /",
      "Loading...":"...",
      "404 Not Found / How did you get here?": " 404 /   ?",
      "Go Home": "  ",
      "Add Places": " ",
      "Add": "",
    }
  }
};

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    resources,
    interpolation: {
      escapeValue: false // React already does escaping
    }
  });

export default i18n;
